import * as Yup from '../../snowpack/pkg/yup.js'

const validationSchema = [
  Yup.object().shape({
    people: Yup.array().of(Yup.object().shape({
      name: Yup.string().required('Its ok, you can name them')
    })).min(2, 'If you ate alone you dont need this!')
  }),
  Yup.object().shape({
    plates: Yup.array().of(Yup.object().shape({
      name: Yup.string().required('Was it good?'),
      price: Yup.number().required('It wasnt free lol').positive('You got paid to eat??').integer(),
      eatenBy: Yup.array().of(Yup.string()).min(1, 'Someone has to eat it!')
    })).min(1, 'No food, no website')
  }),
  Yup.object().shape({
    price: Yup.number().required('It wasnt free lol').positive('You got paid to eat??').integer(),
    tip: Yup.number().required('If you didnt tip this app breaks').positive('You got paid to eat??').integer()
  })
]

export default validationSchema
